import { EventEmitter } from 'eventemitter3';
import { ProcessingRequest, ProcessingResponse, ProcessingTier, QualityMetrics } from '../types/llm-types';

export interface ProcessorEvents {
  'processing:started': (requestId: string) => void;
  'processing:progress': (requestId: string, progress: number) => void;
  'processing:completed': (requestId: string, response: ProcessingResponse) => void;
  'processing:error': (requestId: string, error: Error) => void;
  'quality:calculated': (requestId: string, metrics: QualityMetrics) => void;
}

export abstract class BaseProcessor extends EventEmitter<ProcessorEvents> {
  protected tier: ProcessingTier;
  protected isProcessing: boolean = false;
  protected activeRequests: Map<string, AbortController> = new Map();

  constructor(tier: ProcessingTier) {
    super();
    this.tier = tier;
  }

  abstract process(request: ProcessingRequest): Promise<ProcessingResponse>;

  protected abstract generateResponse(request: ProcessingRequest): Promise<string>;
  protected abstract calculateQuality(response: string, request: ProcessingRequest): Promise<QualityMetrics>;

  protected async processWithTimeout(
    request: ProcessingRequest,
    timeoutMs: number = this.getDefaultTimeout()
  ): Promise<ProcessingResponse> {
    const abortController = new AbortController();
    this.activeRequests.set(request.id, abortController);

    try {
      this.emit('processing:started', request.id);
      
      const startTime = Date.now();
      
      // Generate response with timeout
      const responsePromise = this.generateResponse(request);
      const timeoutPromise = new Promise<never>((_, reject) => {
        setTimeout(() => reject(new Error('Processing timeout')), timeoutMs);
      });

      const content = await Promise.race([responsePromise, timeoutPromise]);
      
      // Calculate quality metrics
      const qualityMetrics = await this.calculateQuality(content, request);
      this.emit('quality:calculated', request.id, qualityMetrics);

      const processingTime = Date.now() - startTime;

      const response: ProcessingResponse = {
        id: crypto.randomUUID(),
        requestId: request.id,
        content,
        tier: this.tier,
        qualityScore: qualityMetrics.overallScore,
        confidence: this.calculateConfidence(qualityMetrics),
        processingTime,
        model: this.getModelName(),
        reasoning: this.extractReasoning(content),
        citations: this.extractCitations(content),
        metadata: {
          tokensUsed: this.estimateTokenCount(content),
          modelsInvolved: [this.getModelName()],
          processingSteps: this.getProcessingSteps(request),
          qualityMetrics,
          userInterventions: []
        }
      };

      this.emit('processing:completed', request.id, response);
      return response;

    } catch (error) {
      this.emit('processing:error', request.id, error as Error);
      throw error;
    } finally {
      this.activeRequests.delete(request.id);
    }
  }

  async cancelRequest(requestId: string): Promise<void> {
    const controller = this.activeRequests.get(requestId);
    if (controller) {
      controller.abort();
      this.activeRequests.delete(requestId);
    }
  }

  protected getDefaultTimeout(): number {
    const timeouts: Record<ProcessingTier, number> = {
      atomic: 1000,     // 1s
      moderate: 5000,   // 5s  
      complex: 15000,   // 15s
      expert: 45000     // 45s
    };
    return timeouts[this.tier];
  }

  protected calculateConfidence(metrics: QualityMetrics): number {
    // Weighted confidence calculation
    return (
      metrics.coherence * 0.3 +
      metrics.relevance * 0.3 +
      metrics.accuracy * 0.2 +
      metrics.completeness * 0.2
    );
  }

  protected extractReasoning(content: string): string[] {
    // Extract reasoning steps from content
    const reasoningPattern = /(?:step \d+|first|second|third|finally|therefore|because|since)[\s\S]*?(?=\n\n|\n[A-Z]|$)/gi;
    return content.match(reasoningPattern) || [];
  }

  protected extractCitations(content: string): string[] {
    // Extract citations and sources
    const citationPattern = /\[.*?\]|\(.*?\)|source:.*$/gim;
    return content.match(citationPattern) || [];
  }

  protected estimateTokenCount(content: string): number {
    // Rough token estimation (4 characters per token average)
    return Math.ceil(content.length / 4);
  }

  protected abstract getModelName(): string;
  protected abstract getProcessingSteps(request: ProcessingRequest): any[];

  async getHealth(): Promise<ProcessorHealth> {
    return {
      tier: this.tier,
      isHealthy: !this.isProcessing || this.activeRequests.size < 5,
      activeRequests: this.activeRequests.size,
      averageProcessingTime: await this.getAverageProcessingTime(),
      successRate: await this.getSuccessRate()
    };
  }

  protected async getAverageProcessingTime(): Promise<number> {
    // Implement based on historical data
    return 0;
  }

  protected async getSuccessRate(): Promise<number> {
    // Implement based on historical data
    return 100;
  }
}

export interface ProcessorHealth {
  tier: ProcessingTier;
  isHealthy: boolean;
  activeRequests: number;
  averageProcessingTime: number;
  successRate: number;
} 